import Vue from "vue"; // vue   from"vue" 
// import Vuex, { Store } from "vuex";
import Vuex from "vuex";
// 
Vue.use(Vuex);

// vuex     main.js
let store = new Vuex.Store({
  //
  state: {
    author:'lijj',
    isLogin:'',
  },
  getters: {
    
  },
  mutations: {
    newAuthor(state,msg){
      state.author = msg;
    },
    isLogin(state,msg){
      state.isLogin = msg;
      localStorage.setItem('isLogin',msg);
    }
    
  }
});

export default store; //store



