import Vue from 'vue';
import Router from 'vue-router';

import "@/assets/css/official_common.css";
import "@/assets/css/common.css"; // css   .css
import "@/assets/css/index_right.css";
import "@/assets/css/sign.css";
import "@/assets/css/jquery-ui-1.10.4.custom.css";
import "@/assets/css/swiper.min.css";

Vue.use(Router);

import home from "@/components/home";
//import Main from "@/components/main";
import news from "@/components/news";
import guide from "@/components/guide";
import giftcode from "@/components/giftcode";

import fuwuqi from "@/components/fuwuqi";
import xiangqing from "@/components/xiangqing";

import rightbar from "@/components/center-right-box";
//import leftbar from "@/components/leftbar";
import screenshot from "@/components/screenshot";
import logbox from "@/components/logbox";
import peopleshow from "@/components/people-show";
//  
import hot from "@/components/news/hot";
import events from "@/components/news/events";
import all from "@/components/news/all";

import serverList from "@/components/serverList"

 let router = new Router({ //router main.js 
  mode:"hash",
  linkActiveClass:'active',
  //
  scrollBehavior(to, from, savePosition) {
    // 
    //console.log(to); // 
    //console.log(from); // 
    //console.log(savePosition); //   

    // if (savePosition) {
    //   return savePosition;
    // } else {
    //   return { x: 0, y: 0 }; //xy 
    // }

    //   
    if (to.hash) {
      return {
        selector: to.hash  // selector   
      };
    }
  },
  routes: [
    {
      path: "/",  
      //    
      components: {
        //
        default: home, // 
        logbox:logbox,
        peopleshow:peopleshow,
        rightbar: rightbar ,//  <router-view name = "slider"></router-view>  name   
        bottom:screenshot
      },
    },

    {
      path: "/home",
      name: "home",
      //    
      components: {
        //
        default: home, // 
        logbox:logbox,
        peopleshow:peopleshow,
        rightbar: rightbar ,//  <router-view name = "slider"></router-view>  name   
        bottom:screenshot
      },
    },
    {
      path: "/news",//:  :id  /user/1  /user/2  /user   ?0 1  2 
      //name: "news",
      components: {
        default: news,
        logbox:logbox,
        peopleshow:peopleshow,
        rightbar: rightbar ,
        bottom:screenshot
      },
      children: [
        {
          path: "", // /news 
          name: "news",
          component: all,
        },
        {
          path: "hot", 
          name: "hot",
          component: hot,
          
        },
        {
          path: "events", 
          name: "events",
          component: events,
        },
        {
          path: "xiangqing",
          name: "xiangqing",
          component:xiangqing
        }
      ]

    },
    // {
    //   path: "/news",
    //   name: "news",
    //   components: {
    //     default: news, 
    //     logbox:logbox,
    //     peopleshow:peopleshow,
    //     rightbar: rightbar ,
    //     bottom:screenshot
    //   },
    // },
    {
      path: "/guide",
      name: "guide",
      components: {
        default: guide, 
        logbox:logbox,
        peopleshow:peopleshow,
        rightbar: rightbar ,
        bottom:screenshot
      },
    },
    {
      path: "/giftcode",
      name: "giftcode",
      components: {
        default: giftcode, 
        logbox:logbox,
        peopleshow:peopleshow,
        rightbar: rightbar ,
        bottom:screenshot
      },
      // beforeEnter: (to, from, next) => {
      //   alert('')
      // }
    },
    {
      path: "/fuwuqi",
      //name: "fuwuqi",
      components: {
        default: fuwuqi, 
        logbox:logbox,
       // peopleshow:peopleshow,
        rightbar: rightbar ,
        bottom:screenshot
      },
      children:[
        {
          path:'*',
          redirect:serverList

        },
        {
          path:'',
          name: "fuwuqi",
          component:serverList

        },
        // {
        //   path:'serverList',
        //   name:'serverList',
        //   component:serverList

        // }
      ],
      meta:{
        index:0,
        login :true,
      }

    },

    //
    // {
    //   path: "*",
    //   component: home,//
    //   redirect: { path: "/home" },
    // }
    

  ]
})

// 
//
// router.beforeEach((to, from , next)=>{
//   alert('')
//   // if(to.path == "/news"){
//   //    next()
//   // }else{
//   //   alert('')
//   // }
 
// })


export default router


